import React from 'react'
import Header from "../components/Header/Header";
import Home from "../pages/Home";
import Life from "../pages/Life";
import Technique from "../pages/Technique";
import About from "../pages/About";
import Message from "../pages/Message";
import {Switch,Route} from 'react-router-dom'
import Auth from "../guard/Auth";
import {connect} from "react-redux";
import {withRouter} from 'react-router-dom'
import Detail from "../pages/Detail";
import AuthDetail from '../guard/AuthDetail'


class App extends React.Component{
    render() {
        return (
            <div>
                <Header></Header>
                <Switch>
                    <Auth path='/home' component={Home}></Auth>
                    <Route path='/life' component={Life}></Route>
                    <Route path='/technique' component={Technique}></Route>
                    <Route path='/about' component={About}></Route>
                    <Route path='/message' component={Message}></Route>
                    <AuthDetail path='/detail' component={Detail}></AuthDetail>
                    <Auth path='/' component={Home}></Auth>

                </Switch>
            </div>

        );
    }

    componentWillReceiveProps(nextProps){//props改变时
        if(this.props.location !== nextProps.location){//当前地址不等于目标地址
            window.scrollTo(0,0);//滚动到顶部
        }
    }

}

const  mapStateToProps=(state,ownProps)=>{
    return {}
}

const  mapDispatchToProps=(dispatch)=>({
    hello:()=>{
        console.log('hello');
    }
})




export  default connect(mapStateToProps,mapDispatchToProps)(withRouter(App))