import React from 'react';
import {Icon} from 'antd';
import headerStyle from '../../assets/css/header.module.css';
import NavStyle from "../../assets/css/headerNav.module.css";
import {NavLink} from "react-router-dom";
import QueueAnim from 'rc-queue-anim';
export  default class  Header extends React.Component{
    state={
        navEvent:false
    }
    render(){
        return (
            <div className={headerStyle.header} style={{zIndex:200}}>
                <div className={headerStyle.top}>
                    <div className={headerStyle.top_left} onClick={this.navShow.bind(this)}>
                        <Icon type='menu-fold'/>
                    </div>
                    <div className={headerStyle.top_right}>个人博客</div>
                </div>
                <div id='zhzhz' style={this.state.navEvent?{display:'block'}:{display:'none'}} onClick={this.disapper.bind(this)}>
                    <ul className={NavStyle.nav_ul}>
                        <QueueAnim forcedReplay={true}>
                            {this.state.navEvent ? [
                                <li key='demo1'><NavLink to='/home' className={NavStyle.ab}>主页</NavLink></li>,
                                <li key='demo2'><NavLink to='/life' className={NavStyle.ab}>生活</NavLink></li>,
                                <li key='demo3'><NavLink to='/technique' className={NavStyle.ab}>技术</NavLink></li>,
                                <li key='demo4'><NavLink to='/about' className={NavStyle.ab}>关于我</NavLink></li>,
                                <li key='demo5'><NavLink to='/message' className={NavStyle.ab}>留言板</NavLink></li>,
                            ]:null}
                        </QueueAnim>
                    </ul>
                </div>
            </div>
        );
    }
    disapper(){
        let ulnode=document.getElementById('zhzhz');
        ulnode.style.display='none';
        this.setState({navEvent:!this.state.navEvent})
        console.log(ulnode)
    }
    navShow(ev){
        this.setState({navEvent:!this.state.navEvent})
    }
}