import React from 'react';
import {Route,Redirect} from 'react-router-dom'


class Auth extends React.Component{

  state={
    hasAuth:false,
    auth:false,
    data:{}
  };

  componentDidMount(){
    let id=(this.props.location.search.slice(1));

    fetch(
      'http://localhost:8011/api/column/'+id
    ).then(
      res=>res.json()
    ).then(
      res=>{
        console.log(res.data)
        this.setState({
        hasAuth:true,
        auth:true,
        data:res.data
      })
      }
    )
  }

  render(){


    let {component:Component} = this.props; // component == User类
    // 异步条件

    if (!this.state.hasAuth ){
      return null;
    }

    return <Route  render={(props)=>(
      this.state.auth ? <Component data={this.state.data} {...props} /> : <Redirect to="/home"/>
    )} />

  }

}



export default Auth