import React from 'react';
import HomeStyle from '../assets/css/home.module.css'
import { Tag,Icon } from 'antd';
import {Link} from 'react-router-dom'
import {action} from '../store/action'
import {connect} from 'react-redux'

class Home extends React.Component{
    flag=0;
    render() {
        return (
            <div className={HomeStyle.home}>
                <div style={{height:35}}></div>
                <div className={HomeStyle.hot_posts}>
                    <h2 className={HomeStyle.title}>热门排行</h2>
                    <ul>

                        {
                            this.props.topList.map((value,index)=>
                                <li key={index}>
                                    <p>
                                <span className={HomeStyle.muted}>
                                    <span to='/detail' className={HomeStyle.action}>
                                        <Icon type='heart' className={HomeStyle.fa} />
                                        <span className="count" style={{display:'inline-block'}}>&nbsp;{value.follow}&nbsp;</span>
                                        喜欢
                                    </span>
                                </span>
                                    </p>
                                    <Tag color="#f50" className={HomeStyle.label}>{index+1}</Tag>
                                    <Link to={'/detail?'+value._id}>{value.title}</Link>
                                </li>
                            )
                        }
                    </ul>
                </div>

                {this.props.homeList.map((value,index)=>
                    <Link to={'/detail?'+value._id} key={index}>
                        <article className={HomeStyle.excerpt}>
                            <header>
                                <Tag color='#f00' className={HomeStyle.label}>{value.tag}</Tag>
                                <h2><a href='javascript:;'>{value.title}</a></h2>
                            </header>
                            <div className={HomeStyle.focus}>
                                <img src={value.img} alt={value.title}/>
                            </div>
                            <span className={HomeStyle.note}>
                                {value.content}
                            </span>
                        </article>
                    </Link>
                )}
            </div>
        );
    }

    componentDidMount() {
        this.props.didHome();
        this.props.addTop();
        window.addEventListener('scroll', this.handleScroll.bind(this));
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll.bind(this));
    }
    handleScroll(e) {
        if(document.documentElement.scrollTop+document.documentElement.clientHeight+10>document.documentElement.scrollHeight){
                this.flag++;
                this.props.addHome(7*this.flag);
        };
    }
}


const  mapStateToProps=(state,ownProps)=>{
    return {
        homeList:state.homeList,
        topList:state.topList
    }
}

const  mapDispatchToProps=(dispatch)=>({
    didHome:()=>{
        dispatch(action({dispatch,
            url:'/api/column',params:{_limit:7},TypeName:'VIEW_HOME'}))
            .then(
            res=>console.log(res)
            )
    },
    addHome:(skip)=>{
        dispatch(action({dispatch,
            url:'/api/column',params:{_limit:7,_skip:skip},TypeName:'ADD_HOME'}))
            .then(
            )
    },
    addTop:()=>{
        dispatch(action({dispatch,
            url:'/api/column',params:{_limit:5,_sort:'follow'},TypeName:'VIEW_TOP'}))
            .then(
                res=>console.log(res)
            )
    }
})



export  default connect(mapStateToProps,mapDispatchToProps)(Home)