import React from 'react';
import mesStyle from '../assets/css/message.module.css'
import {connect} from 'react-redux'
import axios from 'axios'
import formatTime from "../utils/formatTime";

class Message extends React.Component{
    state={
        nickname:'',
        mess:''
    }
    render() {
        return (
            <div className='message'>
                <div style={{marginTop:40}}></div>
                <div className={mesStyle.message__send}>
                    昵 &nbsp;称:<input  type='text' className={mesStyle.nickname} name='nickname' defaultValue={this.state.nickname} onChange={this.change}/>
                    留言板:<textarea name='mess' defaultValue={this.state.mess} onChange={this.change}></textarea>
                    <input  type='button' value='发送' className={mesStyle.message__button} onClick={()=>{
                        let ni=document.getElementsByName('nickname')[0];
                        let mes=document.getElementsByName('mess')[0]
                        this.props.sendMess(this.state.nickname,this.state.mess);
                        this.props.getMess();
                        ni.value='';mes.value='';
                    }}/>
                </div>
                <div className='message__display'>
                    <ul className={mesStyle.messul}>

                        {
                            this.props.mesList.map((value,index)=>
                            <li key={index}>
                                <div className={mesStyle.mesDis_left}><span className={mesStyle.mesDis_img}></span></div>
                                <div className={mesStyle.mesDis_right}>
                                <div className={mesStyle.mesDis_user}><span>{value.nickname}</span><span style={{marginLeft:10}}>{formatTime(Number(value.time))}</span></div>
                                <div className={mesStyle.mesDis_content}>{value.mess}</div>
                                </div>
                            </li>
                        )
                        }

                    </ul>
                </div>
            </div>
        );
    }

    change=(ev)=>{
        this.setState({[ev.target.name]:ev.target.value})
    }


    componentDidMount() {
        this.props.getMess();
    }
}

const  mapStateToProps=(state,ownProps)=>{
    return {
        mesList:state.mesList||[],
    }
}

const  mapDispatchToProps=(dispatch)=>({
    sendMess:(nickname,mess)=>{
            let date=new Date()
            let time=date.getTime();
            axios({
                url:'/api/cart',
                params:{
                    _nickname:nickname,
                    _mess:mess,
                    _time:time
                }
            }).then(
            )
    },
    getMess:()=>{
        axios({
            url:'/api/getmes'
        }).then(
            res=>dispatch({type:"VIEW_MESS",payload:res.data.data})
        )
    }
})


export  default connect(mapStateToProps,mapDispatchToProps)(Message)