export default (state,action)=>{
    let {type,payload} =action;
    switch (type) {
        case "VIEW_HOME":
            return {...state,homeList:payload};
        case "ADD_HOME":
            return {...state,homeList:state.homeList.concat(payload)};
        case "ADD_LIFE":
            return {...state,lifeList:state.lifeList.concat(payload)};
        case "ADD_TECH":
            return {...state,techList:state.techList.concat(payload)};
        case "VIEW_LIFE":
            return {...state,lifeList:payload};
        case "VIEW_TOP":
            return {...state,topList:payload};
        case "VIEW_TECH":
            return {...state,techList:payload};
        case "VIEW_MESS":
            return {...state,mesList:payload}
        default:
            return {...state}
    }
}