import React from 'react';
import aboutStyle from '../assets/css/about.module.css'
import ReactCanvasNest from 'react-canvas-nest';
import {Icon} from 'antd'

export  default  class About extends React.Component{
    render() {
        let cavWidth=document.documentElement.clientWidth;
        let canHeight=document.documentElement.clientHeight;
        return (
            <div className={aboutStyle.about}>
                <div style={{height:40}}></div>
                <div>
                    <ReactCanvasNest className = {aboutStyle.canvasNest} config = {{ pointColor:'255,255,255',lineColor:'255,255,255'}}style={{width:cavWidth,height:canHeight-50,zIndex:50}} />
                    <canvas style={{width:cavWidth,height:canHeight-50}}/>
                </div>
                <div style={{position:'absolute',left:cavWidth/2-20,top:600,zIndex:100}} onClick={this.goDown}>
                    <Icon type='down' style={{fontSize:40,color:'#fff'}}/>
                </div>
                <div className={aboutStyle.about_content}>
                    <h3 className={aboutStyle.title}>个人介绍</h3>
                    <div>
                        <span className={aboutStyle.content_mes}>昵  称:xzcxzc</span>
                        <span className={aboutStyle.content_mes}>职  业:dsadsa</span>
                        <span className={aboutStyle.content_mes}>爱  好:asdasd</span>
                        <span className={aboutStyle.content_mes}>简  介:asdasd</span>
                        <span className={aboutStyle.content_mes}>邮  箱:asdacxz</span>
                        <span className={aboutStyle.content_mes}>Q  &nbsp;Q:zxczcsada</span>
                    </div>
                </div>
            </div>
        );
    }
    goDown=()=>{
        console.log(typeof document.documentElement.scrollTop);
        let timer=setInterval(function(){
            document.documentElement.scrollTop=document.documentElement.scrollTop+100;
            if(document.documentElement.scrollTop>600){
                clearInterval(timer);
            }
        },30)

    }
}