import React from 'react';
import HomeStyle from "../assets/css/home.module.css";
import {Tag} from "antd";
import {Link} from "react-router-dom";
import {action} from "../store/action";
import {connect} from "react-redux";

class Life extends React.Component{
    flag=0;
    render() {
        return (
            <div className={HomeStyle.life}>
                <div style={{height:20}}></div>


                {this.props.lifeList.map((value,index)=>
                    <article className={HomeStyle.excerpt} key={index}>
                        <header>
                            <Tag color='#f00' className={HomeStyle.label}>{value.tag}</Tag>
                            <h2><Link>{value.title}</Link></h2>
                        </header>
                        <div className={HomeStyle.focus}>
                            <img src={value.img} alt={value.title}/>
                        </div>
                        <span className={HomeStyle.note}>
                            {value.content}
                        </span>
                    </article>
                )}
            </div>
        );
    }

    componentDidMount() {
        this.props.didHome();
        window.addEventListener('scroll', this.handleScroll.bind(this));
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll.bind(this));
    }
    handleScroll(e) {
        if(document.documentElement.scrollTop+document.documentElement.clientHeight+10>document.documentElement.scrollHeight){
            this.flag++;
            this.props.addLife(7*this.flag);
        };
    }
}

const  mapStateToProps=(state,ownProps)=>{
    return {
        lifeList:state.lifeList,
    }
}

const  mapDispatchToProps=(dispatch)=>({
    didHome:()=>{
        dispatch(action({dispatch,
            url:'/api/column',params:{_limit:7,_type:'life'},TypeName:'VIEW_LIFE'}))
            .then(
                res=>console.log(res)
            )
    },
    addLife:(skip)=>{
        dispatch(action({dispatch,
            url:'/api/column',params:{_limit:7,_skip:skip,_type:'life'},TypeName:'ADD_LIFE'}))
            .then(
            )
    }
})



export  default connect(mapStateToProps,mapDispatchToProps)(Life)